const defaultLanguage = localStorage.getItem("language") || "en";

//  i18next
i18n.use(initReactI18next).init({
    resources,
    lng: defaultLanguage,
    fallbackLng: "en",
    interpolation: {
        escapeValue: false,
    },
    detection: {
        order: ["localStorage", "navigator"],
        caches: ["localStorage"],
    },
});

i18n.on("languageChanged", (lang) => {
    localStorage.setItem("language", lang);
});

export default i18n;
