import React, { useState } from "react";
import { Helmet } from "react-helmet";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";

import Blog1 from "../../../images/blog/api-rest.png";
import FunFactsBox from "../FunFactsBox";
import FeedbackEmpty from '../../../images/profile/photo-feedback-empty.png'
import Feedback1 from '../../../images/profile/feedback-rz2.jpeg'

const ApiAutomation = () => {
    const [options] = useState({
        loop: true,
        margin: 20,
        dots: true,
        nav: false,
        autoplay: true,
        autoplayTimeout: 5000,
        autoplayHoverPause: true,
        responsive: {
            0: { items: 1 },
            600: { items: 2 },
            1000: { items: 3 },
        },
    });
    // Single Item Data
    const servicesData = [
        {
            id: 1,
            icon: "fas fa-code",
            title: "API Fundamentals",
            description: "Learn REST API basics, HTTP requests, status codes, and JSON format.",
        },
        {
            id: 2,
            icon: "fas fa-toolbox",
            title: "Postman Testing",
            description: "Perform manual API testing with Postman and create robust collections.",
        },
        {
            id: 3,
            icon: "fas fa-tools",
            title: "Automation Tools",
            description: "Work with Rest Assured, TestNG, Maven, and Jackson for automated tests.",
        },
        {
            id: 4,
            icon: "fas fa-database",
            title: "JSON Parsing",
            description: "Efficiently handle JSON data and integrate dynamic values in your tests.",
        },
        {
            id: 5,
            icon: "fas fa-bolt",
            title: "API Framework",
            description: "Build a test framework from scratch with reusable methods and endpoints.",
        },
        {
            id: 6,
            icon: "fas fa-cloud-upload-alt",
            title: "CI/CD Integration",
            description: "Automate test execution with GitLab pipelines and generate detailed reports.",
        },
        {
            id: 7,
            icon: "fas fa-calendar-check",
            title: "Scheduled Test Runs",
            description: "Set up test suites for automated runs and trigger them on a schedule.",
        },
        {
            id: 8,
            icon: "fas fa-shield-alt",
            title: "Token Management",
            description: "Securely handle API tokens and integrate them dynamically in your tests.",
        },
    ];

    // Testimonials Data
    const testimonialsData = [
        {
            id: 1,
            image: Feedback1,
            name: "Georgii H.",
            text: "  , .",
            rating: 5,
        },
        {
            id: 2,
            image: FeedbackEmpty,
            name: "Yuriy F.",
            text: "Great content, step-by-step guidance. ",
            rating: 5,
        },
        {
            id: 3,
            image: FeedbackEmpty,
            name: "",
            text: "   )    -   .",
            rating: 5,
        },
        {
            id: 4,
            image: FeedbackEmpty,
            name: "",
            text: "      ,       ",
            rating: 5,
        },
    ];

    return (
        <React.Fragment>
            <Helmet>
                {/* SEO Page title tag */}
                <title>Mykola Shchypailo | API Automation</title>
                {/* Meta description */}
                <meta name="description" content="Learn programming with Mykola Shchypailo: courses, tips, and resources for developers." />;

                {/* Twitter card meta tags */}
                <meta name="twitter:card" content="summary_large_image" />
                <meta name="twitter:image" content="https://avstechnolabs.com/og-images/seo-banner.png" />
                <meta name="twitter:title" content="Mykola Shchypailo | Programming courses" />
                <meta name="twitter:creator" content="@mavi" />
                <meta name="twitter:site" content="@mavi" />
                <meta name="twitter:description" content="Learn programming with Mykola Shchypailo: courses, tips, and resources for developers." />

                {/* Facebook card meta tags */}
                <meta property="og:type" content="website" />
                <meta property="og:url" content="https://nick8787.github.io/m-shchypailo" />
                <meta property="og:title" content="Mykola Shchypailo | Programming courses"
                />
                <meta property="og:description" content="Learn programming with Mykola Shchypailo: courses, tips, and resources for developers."
                />
                <meta property="og:image" content="https://avstechnolabs.com/og-images/seo-banner.png"
                />
            </Helmet>
            {/* About Section Start  */}
            <section id="about" className="section active">
                <div className="homecolor-box"></div>
                <div className="common_bg animate__animated animate__fadeInDown">
                    <div className="container">
                        <div className="about-content">
                            {/* About Title Start */}
                            <div className="row ">
                                <div className="col-12 ">
                                    <div
                                        className="section-title animate__animated animate__fadeInDown">
                                        <p className="common-desctiption">My Course</p>
                                        <h1 className="common-title">API <span>Automation</span> + <span>Postman</span> + <span>Ci/CD</span></h1>
                                        <div className="animated-bar"></div>
                                    </div>
                                </div>
                            </div>
                            {/* About Title End  */}
                            {/*  */}
                            <div
                                className="col-12 text-center animate__animated animate__fadeInUp"
                            >
                                <img
                                    src={Blog1}
                                    alt="Spring Boot"
                                    className="img-fluid custom-image"
                                    style={{
                                        maxWidth: "60%",
                                        height: "auto",
                                        marginBottom: "30px",
                                        boxShadow: "0 15px 30px rgba(0, 0, 0, 0.5)",
                                        borderRadius: "8px",
                                    }}
                                />
                                <style>
                                    {`
      @media (max-width: 768px) {
        .custom-image {
          max-width: 100% !important;
        }
      }
    `}
                                </style>
                            </div>
                            {/* Service Section   */}
                            <div className="services-section text-center">
                                <div className="row">
                                    <div className="col-12">
                                        <div
                                            className="section-title animate__animated animate__fadeInUp animate__slower">
                                            <h1 className="common-title">About <span>Course</span></h1>
                                            <div className="animated-bar"></div>
                                        </div>
                                        <p className="service-text animate__animated animate__fadeIn">
                                            This course will teach you the fundamentals of <span
                                            className="highlight-skill">API Automation</span> using <span
                                            className="highlight-skill">Postman</span>, <span
                                            className="highlight-skill">Rest Assured</span>, and <span
                                            className="highlight-skill">Java</span>. You'll create robust frameworks
                                            from scratch, work with <span
                                            className="highlight-skill">JSON parsing</span>, and integrate <span
                                            className="highlight-skill">CI/CD pipelines</span> for seamless automation.
                                        </p>
                                    </div>
                                </div>
                                <div
                                    className="row animate__animated animate__zoomIn animate__slower">
                                    {servicesData.map((service) => (
                                        <div key={service.id}
                                             className="col-lg-3 col-sm-6 services-box animate__animated animate__zoomIn animate__slower animate__delay-1s">
                                            <div className="service-item">
                                                <i className={service.icon}></i>
                                                <h4><span className="service-line">{service.title}</span></h4>
                                                <p>{service.description}</p>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                            {/* Testimonials Section */}
                            <div className="row">
                                <div className="col-12 animate__animated animate__fadeInDown animate__delay-1s">
                                    <div className="section-title">
                                        <p className="common-desctiption">What my students say</p>
                                        <h1 className="common-title">My <span>Feedback</span></h1>
                                        <div className="animated-bar"></div>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <OwlCarousel className="owl-theme" id="testimonial" {...options}>
                                    {testimonialsData.map((testimonial) => (
                                        <div key={testimonial.id} className="item">
                                            <div className="testimonial-item">
                                                <div className="quote">
                                                    <i className="fas fa-quote-left"></i>
                                                </div>
                                                <div className="testimonial-img">
                                                    <img src={testimonial.image} alt={`testimonial-${testimonial.id}`}/>
                                                </div>
                                                {/*   */}
                                                <h5 className="highlight-skill">{testimonial.name}</h5>
                                                {/*   */}
                                                <p dangerouslySetInnerHTML={{__html: testimonial.text}}/>
                                                <div className="rating">
                                                    {Array.from({length: testimonial.rating}, (_, index) => (
                                                        <i key={index} className="fas fa-3x fa-star"></i>
                                                    ))}
                                                </div>
                                            </div>
                                        </div>
                                    ))}
                                </OwlCarousel>
                            </div>
                            {/* Fun Fact Section */}
                            <div className="funfacts-section">
                                <div className="row justify-content-center">
                                    <div className="col-12">
                                        <div className="section-title">
                                            <p className="common-desctiption">Here's what we've achieved</p>
                                            <h1 className="common-title">Course <span>Stats</span></h1>
                                            <div className="animated-bar"></div>
                                        </div>
                                    </div>
                                </div>
                                <div className="row justify-content-center">
                                    <div className="col-lg-3 col-md-6 d-flex justify-content-center">
                                        <div className="funfacts-box">
                                            <FunFactsBox endValue={500}/>
                                            <p className="fun-text">Enrolled <span>Students</span></p>
                                        </div>
                                    </div>
                                    <div className="col-lg-3 col-md-6 d-flex justify-content-center">
                                        <div className="funfacts-box">
                                            <FunFactsBox endValue={25} />
                                            <p className="fun-text">Positive <span>Feedback</span></p>
                                        </div>
                                    </div>
                                    <div className="col-lg-3 col-md-6 d-flex justify-content-center">
                                        <div className="funfacts-box">
                                            <FunFactsBox endValue={3} suffix="h" />
                                            <p className="fun-text">Video <span>Lessons</span></p>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </section>
            {/* About Section End  */}
        </React.Fragment>
    )
}


export default ApiAutomation;
